<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: 'app',
    computed: {
      ...mapState(['siteName'])
    },
    methods: {
      getBaseData () {
        this.$store.dispatch('getBaseData')
      }
    },
    components: {
    },
    mounted () {
      this.getBaseData()
    }
  }
</script>

<style lang="less">
</style>



