import marked from 'marked'
import { APIArticle } from '@/api/index'
import * as types from '@/store/mutation-types'

export default {
  state: {
    articleList: [],
    curArticle: {
      id: '',
      title: '',
      desc: '',
      time: '',
      tags: [],
      mdContent: ''
    }
  },
  getters: {
    ArticleContent (state) {
      return marked(state.curArticle.mdContent, { sanitize: true })
    }
  },
  actions: {
    getArticleList ({ commit }, { pageIndex }) {
      APIArticle.getArticleList({ pageIndex }).then((data) => {
        const articleList = data.data.articleList
        commit(types.getArticleListSuccess, articleList)
      }, () => {
        commit(types.getArticleListError)
      })
    },
    getSingleArticle ({ commit }, { id }) {
      if (!id) {
        commit(types.getArticleError)
        return
      }
      const arr = id.split('-')
      const pageIndex = arr[0]
      const artIndex = arr[1]
      if (arr.length !== 2 || !pageIndex || !artIndex || artIndex < 1 || artIndex > 15) {
        commit(types.getArticleError)
        return
      }
      APIArticle.getArticleList({ pageIndex }).then((data) => {
        const artList = data.data.articleList
        const curArt = artList[artIndex - 1]
        return Promise.resolve(curArt)
      }, () => {
        return Promise.reject(new Error('getArticleListError'))
      }).then((data) => {
        APIArticle.getSingle({ title: data.title }).then((singleData) => {
          data.mdContent = singleData.data
          commit(types.getArticleSuccess, data)
        }, () => {
          commit(types.getArticleError)
        })
      }, () => {
        commit(types.getArticleError)
      })
    },
    clearArticle ({ commit }) {
      commit(types.clearArticle)
    }
  },
  mutations: {
    [types.getArticleListSuccess] (state, articleList) {
      state.articleList = state.articleList.concat(articleList)
    },
    [types.getArticleListError] (state) {
    },
    [types.getArticleSuccess] (state, curArt) {
      state.curArticle.id = curArt.id
      state.curArticle.title = curArt.title
      state.curArticle.desc = curArt.desc
      state.curArticle.time = curArt.time
      state.curArticle.tags = curArt.tags
      state.curArticle.mdContent = curArt.mdContent
    },
    [types.getArticleError] (state) {
    },
    [types.clearArticle] (state) {
      state.curArticle = {
        id: '',
        title: '',
        desc: '',
        time: '',
        tags: [],
        mdContent: ''
      }
    }
  }
}



