<template lang="html">
  <b-container fluid>
    <b-row>
      <b-col md="4" lg="3" class="work-left">
        <div class="mine">
          <div class="mine-header"></div>
          <div class="mine-body">
            <b-img rounded="circle" src="https://gitee.com/uploads/19/409119_Arweil.png?1489311697" alt="img" />
            <div class="mine-intro">
              <h3 class="name">{{yourName}}</h3>
              <p>{{sign}}</p>
              <p>
                <i class="iconfont icon-map"></i>
                {{addr}}
              </p>
              <p>
                <i class="iconfont icon-email"></i>
                {{email}}
              </p>
            </div>
          </div>
          <div class="mine-footer">
            <a class="iconfont icon-git" v-bind:href="git" target="_blank"></a>
            <a class="iconfont icon-sina" v-bind:href="sina" target="_blank"></a>
          </div>
        </div>
      </b-col>
      <b-col md="8" lg="9" class="work-right">
        <router-view class="mt-4"></router-view>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
  import { mapState } from 'vuex'
  export default {
    name: 'blog-work',
    computed: {
      ...mapState(['yourName', 'sign', 'addr', 'email', 'company', 'job', 'git', 'sina'])
    },
    methods: {
    },
    mounted () {
    }
  }
</script>

<style lang="less" scoped>
  .work-left {
    padding: 0;
    height: 100vh;
    background-color: #1E2224;
  }

  .work-right {
    height: 100vh;
    overflow-y: scroll;
    background-color: #F5F5F7;
  }

  .mine {
    position: relative;
    text-align: center;
    height: 100%;
    .mine-header {
      background: url('../assets/img/mountain.jpg');
      background-size: cover;
      min-height: 150px;
    }

    img {
      margin-top: -70px;
      margin-bottom: 10px;
      border: 3px solid #fff;
      max-width: 100px;
    }

    .mine-body {
      padding: 1.25rem;
      color: #fff;
    }

    .mine-intro {
      .name {
        margin-bottom: 25px;
      }
      p {
        margin-bottom: 20px;
      }
    }

    .mine-footer {
      position: absolute;
      bottom: 0;
      padding: 1.25rem;
      width: 100%;
      .iconfont {
        margin-right: 20px;
        font-size: 26px;
        text-decoration: none;
        color: #fff;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
</style>




